import React from 'react'
import Social from '../../blocks/social'
import Users from '../../blocks/users'

const Tile1 = (props) => {
    const {type, categories, levels, selected, className} = props;
    const {comments, votes, shared} = props;
    
    const {post = {}} = props;
    const {title, text, podcast} = post;


    const getTypes = () => {

    }

    return (
        <div className={`cell size1 ${selected ? 'selected' : ''} ${className || ''}`}>
            <div className="header">
                {getTypes()} {title}
            </div>
            <div className="content">
                <div className="body">
                {text}

                {post.type === 4 &&
                
                <div className="podcast"><img src={`/media/images/podcast.png`} /></div>
                
                }
                </div>
                <div className="footer">
                    Posted by <Users users={[post.postedBy]} />
                    <Social social={ {
        nbComments: 0,
        nbVotes: {
            up: 0,
            down: 0
        },
        nbShared: 0,
        levels: post.levels,
        }} />
                    
                </div>
            </div>



            <style jsx> 
        {`
        .podcast {
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        .content {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            height: 230px;
        }

        .cell:last-of-type {
            margin-right: 0px;
        }
        .row:last-of-type {
            margin-bottom: 0px;
        }
        .cell {
            background-color: #F4F6F6;
            border: 1px solid #F0F2F2;
            border-radius: 5px;
            margin-right: 11px;
            height: 282px;

            color: #707070;
            letter-spacing: 0;
        }

        .cell:hover, .selected {   
            background-color: #D3DDDD;
            border: 1px solid #4E7876;
            color: #000000;
            opacity: 1;
        }

        .cell.size1 {
            width: 325px;
        }

        .cell.size2 {
            width: 663px;
        }

        .cell.size3 {
            width: 325px;
            height: 577px;
        }

        .cell.featured {
            border: 1px solid #C2CCCC;
        }
        .featured .header {
            border-bottom: 2px solid #D3DDDD;
        }

        .cell.available {
            border: 2px dashed #C2CCCC;
            background-color: #fff; 
            width: 323px;
            height: 280px;
        }
        
        .available .header {
            border-bottom: 2px dashed #D3DDDD;
        }

        .row {
            display: flex;
            margin-bottom: 11px;
        }
        
        .row.colspan { 
            flex-flow: wrap;
            width: 676px;
            margin-bottom: 0px !important;
        }

        .row.colspan .cell { 
            margin-bottom: 11px;
        }

        .last { 
            margin-bottom: 0px !important;
        }

        .header, .body, .footer {
            padding: 0px 28px 0px 28px;
        }


        .body {
            margin-top: 13px;
            font: 12px/17px Arial;
        }

        .actions {
            height: 100%;
            align-items: center;
            display: flex;
            flex-direction: row;
        }

        

        .header {
            height: 100%;
            align-items: center;
            display: flex;
            flex-direction: row;

            height: 40px;
            border-bottom: 2px solid #FFFFFF;
            
            font: Bold 16px/19px Arial;
        }

        `}
            
        </style>
        
        </div>
    );

}

export default Tile1;